.mybook {
  display: flex;
  font-size: 1.5rem;
  background-color: white;
  border-radius: 10px;
  padding: 10px;
  justify-content: space-between;
  align-items: center;
  box-shadow: 2px 5px lightgrey;
  flex-wrap: wrap;
}
.mybook img {
  max-height: 20rem;
  max-width: 10rem;
  padding-right: 10px;
}
.mybook button {
  background-color: orange;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.25rem;
  height: fit-content;
}
